@import url("https://fonts.googleapis.com/css2?family=Baloo+Bhai+2&family=Merriweather&family=Montserrat&family=Poppins&display=swap");

.nav--bar {
  background-color: #f55a5a;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 0px;
  color: white;
}

.nav--bar img {
  width: 30px;
  cursor: pointer;
}

.nav--bar h1 {
  font-size: 20px;
  margin-left: 8px;
  font-weight: 300;
}

.hero {
  padding: 10px 30px;
  width: 100%;
}

.card {
  color: #2b283a;
  display: flex;
  background-color: rgb(234, 234, 234);
  align-items: center;
  border-radius: 10px;
  justify-content: flex-start;
  padding: 25px;
  height: 30vh;
  margin: 5px 0px;
}

.card img {
  width: 200px;
  height: 230px;
  border-radius: 8px;
  object-fit: cover;
  margin: 0px 30px;
}

.card .location {
  display: flex;
}

.card .location h5,
p {
  margin: 0px 4px;
}

.card h1 {
  margin: 10px 3px;
  font-size: 25px;
}

.card a {
  color: gray;
  margin-left: 10px;
  font-size: 15px;
}

.card i {
  margin: 0px 4px;
  color: rgb(205, 60, 60);
}

.card h5 {
  font-size: 16px;
  letter-spacing: 1px;
}

.card .date {
  font-weight: 600;
  margin-bottom: 8px;
  margin-left: 3px;
  letter-spacing: 1px;
}
